.popup__container {
  width: 100%;
  height: 100vh;
  background-color: #fff;
  top: 0;
  display: block;
  flex-direction: column;
  z-index: 11;
  position: relative;
  padding: 104px 16px 0 16px;
  box-sizing: border-box; }

@media screen and (min-width: 450px) {
  .popup__container {
    max-width: 320px;
    box-shadow: 16px 0 52px 0 rgba(14, 24, 80, 0.2);
    position: fixed; } }

@media screen and (min-width: 1366px) {
  .popup__container {
    min-width: 246px;
    height: auto;
    position: static;
    box-shadow: none; } }
